          <li><a href="">零售测绘</a></li>
          <li><a href="">物流媒体</a></li>
          <li><a href="">餐饮交通</a></li>
        </ul>
      </div>
      <div class="footer-links">
        <div class="link-category"><strong>关于我们</strong></div>
        <ul>
          <li><a href="">加入我们</a></li>
          <li><a href="">联系我们</a></li>
          <li><a href="">服务条款</a></li>
          <li><a href="">隐私策略</a></li>
        </ul>
      </div>
      <div class="footer-links">
        <div class="link-category"><strong>帮助中心</strong></div>
        <ul>
          <li><a href="">新手入门</a></li>
          <li><a href="">数据上传</a></li>
          <li><a href="">数据分享</a></li>
          <li><a href="">数据可视化</a></li>
        </ul>
