</template>

<script>
  import {imgDir} from '@/utils/staticUrl'
  const prefixCls = 'gtu-footer'

  export default {
    data () {
      return {
        codeImgUrl: `${imgDir}/code.png`
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      wrapClasses () {
        return [`${prefixCls}-content`, 'gtu-container']
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-footer';
  $bg : #333333;
  $itemGap: 30px;

  #{$prefixCls} {
    padding: $itemGap 0 $itemGap/2;
    color: #fff;
    font-size: 14px;
    background-color: $bg;
    &-content {
      display: flex;
      align-items: flex-start;
    }
  }
  .footer-links {
    margin-right: $itemGap*2;
    .link-category {
      margin-bottom: 10px;
    }
    ul {
      line-height: 2;
      a {
        color: #ccc;
      }
    }
    
  }
  .code {
    margin-left: auto;
    img {
      display: block;
      width: 100px;
    }
    text-align: center;
  }
  .copy {
    margin-top: $itemGap/3;
    color: #666;
    font-size: 12px;
    text-align: center;
  }
</style>


